*{
box-sizing: border-box;
}

body{
    
    font-size: 2vw;
    color: black;
    margin: 0;
    padding: 0;
    font-family: roboto,sans-serif;
    background-color: #f4f4f4;
}

.container-header{
    box-shadow: 0 1px 2px rgba(0,0,0,0.07), 
    0 2px 4px rgba(0,0,0,0.07), 
    0 4px 8px rgba(0,0,0,0.07), 
    0 8px 16px rgba(0,0,0,0.07),
    0 16px 32px rgba(0,0,0,0.07), 
    0 32px 64px rgba(0,0,0,0.07);
    display:flex;
    align-items: center;
    background-image: linear-gradient(to bottom,rgb(255, 255, 255),rgb(255, 255, 255),rgb(212, 212, 212));    
    flex-direction: column;
    text-transform: uppercase;
    letter-spacing: .5vw;
    
}

h1{
    color: coral;
    font-size: 5vw; 
}

h5{font-size:1.8vw; text-transform: capitalize;}

.list-group{
    box-shadow: 0 1px 2px rgba(0,0,0,0.07), 
    0 2px 4px rgba(0,0,0,0.07), 
    0 4px 8px rgba(0,0,0,0.07), 
    0 8px 16px rgba(0,0,0,0.07),
    0 16px 32px rgba(0,0,0,0.07), 
    0 32px 64px rgba(0,0,0,0.07);  
    width: 28vw;
    height: 76vh;
    display: flex;
    flex-direction: column;
    border-radius: 2vw;
    list-style-type: none;
    justify-content: space-around;
    border: .3vw solid coral;
    background-image: linear-gradient(to right,rgb(42, 42, 42),rgb(37, 37, 37),rgb(34, 34, 34));    
   }

.list-group-item{
    align-self: center;
    padding: 2vh 2vw;
    margin-right: 10vw;
    justify-self: center;
    text-transform: uppercase;
    letter-spacing: .5vw;
    font-size: 2.5vw;
    text-decoration: none;
    color: white;
    font-weight: 400;
    transition: all 0.3s ease;
}

.list-group-item:focus{
    background-color: coral;
    box-shadow: 0px .4vw .6vw 0.09vw rgba(255, 148, 60, 0.5);
    border-radius: 1vw;
    color: white;
    text-shadow: .05vw -.1vw black;
}

.footer{
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 2vw;
    background-color: coral;
    color: white;
    font-weight: 200;
    border: 0.3vw solid rgb(255, 175, 138);
}

.people{
    display:inline-flex;
    flex-direction: column;
    width: 63vw;
    margin-left: 5vw;
    transition: all 0.6s ease;
}

.navbar{
    display: inline-flex;
}

.about{
    display: inline-flex;
    width: 65vw;
    margin: 2vh 3vw ;
    font-size: 2vw;
}

.title-head {
  display: flex;
  border-radius: 1.5vw;
  align-items: center;
  justify-content: space-between;

}

.title-head button{
  margin: 1vw 2vw;

}


.title-head h1{
    font-size: 3vw;
}

.btn {
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 0.5vw 2vw;
    margin: .1vw;
    border-radius: 1vw;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5vw;
    font-family: inherit;
    justify-self: center;
    align-self: center;
  }
  
  .btn:focus {
    outline: none;
  }
  
  .btn:active {
    transform: scale(0.98);
  }
  
  .btn-block {
    display: block;
    width: 100%;
    height: 6vw;
  }
  
  .patient {
    margin: .5vw;
    padding: 1vh 1vw;
    cursor: pointer;
    user-select: none;
    transition: all .3s ease;
    width: 100%;
  }
  
  .patient:nth-child(odd){
    background-color: rgb(228, 228, 228);
  }
  
  .patient.urgent {
    border-left: 1vw solid green;
  }
  
  .patient h3 {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .login-inner{
    width:100vw;
    display: flex;
    justify-items: center;
    justify-self: center;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    align-self: center;
  }

  .error{
    font-size: 1.5vw;
    color: red;
    border-bottom: .1vw solid red;
  }

  .add-form {
    margin-bottom: 3vw;
  }
  
  .form-control {
    margin: 2vw 0;
  }
  
  .form-control label {
    display: block;
    font-size: 3vw;
    text-align: center;
    color: coral;
  }
  
  .form-control input {
    width: 100%;
    height: 4vw;
    margin: 1vw;
    padding: .3vw .8vw;
    font-size: 1.8vw;
    outline: none;
    border: none;
    border-radius: 1.5vw;
    background-color: rgb(255, 252, 251) 
    
  }
  
  .form-control-check {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .form-control-check label {
    flex: 1;
    font-size: 2vw;
  }
  
  .form-control-check input {
    flex: 2;
    height: 1.5vw;
  }

  .people p{
    color: green;
    font-size: 1.5vw;
    text-align: center;
    
  }

  .home, .profile, .settings{
    display: inline-flex;
    max-width: 58vw;
    flex-direction: column;
    text-align: center;
    margin: 3vw 5vw;
    color:coral;
    justify-items: center;
    align-self: center;
    justify-self: center;
    align-content: center;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
  }


